module.exports = {
	data(){
		return {
			userInfo:{},
			personalList: [
				{
					name: '我的订单',
					icon: 'icon-dingdan11'
				},
				{
					name: '我的拼团',
					icon: 'icon-pintuandingdan'
				},
				{
					name: '领红包',
					icon: 'icon-tubiaolunkuo-1'
				},
				{
					name: '我的积分',
					icon: 'icon-jifen3'
				},
				{
					name: '地址管理',
					icon: 'icon-dizhiguanli'
				},
				{
					name: '账号安全',
					icon: 'icon-dingdan11'
				},
				{
					name: '联系客服',
					icon: 'icon-zhanghaoanquan'
				},
				{
					name: '用户反馈',
					icon: 'icon-tubiaolunkuo-'
				},
				{
					name: '帮助中心',
					icon: 'icon-bangzhuzhongxin'
				},
				{
					name: '退还/售后',
					icon: 'icon-shouhou'
				}
			]
		}
	},
	mounted(){
		let userInfo=this.$mp.query.userInfo;
		if(userInfo){
			this.userInfo=JSON.parse(userInfo);
		}else{
			wx.getUserInfo({
				success:(res)=>{
					this.userInfo=res.userInfo;
				}
			})
		}
	},
	methods: {
		toLogin(){
			if(this.userInfo.nickName)return;
			wx.navigateTo({
				url:"/pages/login/login"
			})
		}
	}
	
}
